<template>
  <div class="container">
    <div v-if="!lists.length" class="no-task">
      还没有完成计划呢！
    </div>
    <el-card
      v-for="(item, name, index) in lists"
      :key="index"
      class="box-card"
      shadow="hover"
    >
      <div class="completed-title">{{ name }}</div>
      <div class="completed-content" v-for="(task, index) in item" :key="index">
        {{ task.title }}
      </div>
    </el-card>
  </div>
</template>

<script>
import { getCompleted } from "../../api/task";

export default {
  data() {
    return {
      lists: {}
    };
  },
  methods: {
    fetch_data() {
      getCompleted(this.$store.getters.token).then(response => {
        this.lists = response.data;
      });
    }
  },
  created() {
    this.fetch_data();
  }
};
</script>

